import { injectGlobal } from 'styled-components';

injectGlobal`

  @import url('https://fonts.googleapis.com/css?family=Muli|Roboto');

  body {

    font-family: 'Muli', sans-serif;

    padding : 0;
    margin : 0;

    line-height : 1.5;

  }

`;


